<template lang="pug">
.main-loader 
	h2 tasks&toolz
	b-spinner(label="Spinning")
	b-spinner(type="grow", label="Spinning")
</template>
<script>
import { mapGetters } from "vuex";
export default {
	data: () => ({}),
	mounted() {
		setTimeout(() => {
			if (!this.dataFullLoaded) {
				alert("Failed to load data. Page will be reloaded");
				window.location.reload();
			}
		}, 8000);
	},
	computed: {
		...mapGetters(["dataFullLoaded"])
	}
};
</script>
<style lang="sass" scoped>
.main-loader
	position: fixed
	width: 100%
	height: 100%
	top: 0%
	left: 0%
	background-color: var(--bg2)
	z-index: 4
	display: flex
	align-items: center
	justify-content: center
	font-size: 3rem
	font-weight: 800
	font-family: "Raleway"
	text-transform: uppercase
	color: var(--text1)
</style>