const loc = {
	state: ()=>({
		loc:{}
	}),
	mutations: {
		LOAD_LOC(state,loc){
			state.loc=loc;
		},
	},
	actions: {
		async fetchLoc({commit, rootState}){
			let serverResp=true;
			try{
				const url = "http://ip-api.com/json/";
				const ip = rootState.ip.ip;
				const loc = await (await fetch(`${url}/${ip}`)).json();
				commit('LOAD_LOC', loc);
			}catch(error){
				console.error(error);
				serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{loc:serverResp})
		},
	},
	getters: {
		loc: state=>state.loc,
		countryCode: state=>state.loc.countryCode
	},
}
export default loc