const ip = {
	state: ()=>({
		ip:''
	}),
	mutations: {
		LOAD_IP(state, ip){
			state.ip = ip;
		},
	},
	actions: {
		async fetchIp({commit}){
			const url = "https://api.ipify.org/?format=json"
			let serverResp=true;
			try{
				const ip = await (await fetch(`${url}`)).json()
				commit('LOAD_IP', ip.ip);
			}catch(error){
				console.error(error);
				serverResp=false;
			}
			commit('SET_SERVER_RESPOND',{ip:serverResp})
		}, 
	},
	getters: {
		ip: state=>state.ip
	}
}
export default ip